      </view>
    </view>
    <template v-if="loading">
      <CompanySkeleton v-for="i in 5" :key="i" />
    </template>
    <view class="company-list" :class="{ 'fade-in': !loading }">
      <!-- Âä†ËΩΩÁä∂ÊÄÅ -->
      <template v-if="loading">

      </template>

      <!-- Á©∫Áä∂ÊÄÅ -->
      <view class="empty-state" v-else-if="companies.length === 0">
        <image class="empty-image" src="/static/images/empty.png" mode="aspectFit"></image>
        <text class="empty-text">ÊöÇÊó†Êï∞ÊçÆ</text>
        <text class="empty-tips">Âø´Êù•Ê∑ªÂä†Á¨¨‰∏ÄÊù°ËØÑ‰ª∑Âêß</text>
        <view class="empty-btn" @tap="goToAdd">
          <text class="icon">‚úèÔ∏è</text>
          <text class="text">ÂÜôÁÇπËØÑ</text>
        </view>
      </view>

      <!-- ÂàóË°®ÂÜÖÂÆπ -->
      <template v-else>
        <view v-for="(company, index) in companies" :key="company._id" class="company-card glass-effect"
          :style="{ '--index': index }" hover-class="company-card-hover" @tap="goToDetail(company._id)">
          <view class="card-header">
            <text class="company-name">{{ company.companyName }}</text>
            <view class="rating-wrapper">
              <uni-rate :value="company.rating" size="15"/>
              <text class="rating-text">{{ company.rating || 0 }}</text>
            </view>
          </view>

          <view class="card-content">
            <view class="cons-box" v-if="company.cons">
              <text class="cons-text" :class="{ 'truncated': !company.isExpanded }">{{ company.cons }}</text>
              <text v-if="isTextOverflow(company.cons)" class="expand-btn" @tap.stop="toggleExpand(company)">
                {{ company.isExpanded ? 'Êî∂Ëµ∑' : 'Êü•ÁúãÊõ¥Â§ö' }}
              </text>
            </view>
          </view>

          <view class="card-footer">
            <view class="stats">
              <view class="stat-item">
                <text class="icon">üëÅÔ∏è</text>
                <text class="count">{{ company.viewCount || 0 }}</text>
              </view>
              <view class="stat-item">
                <text class="icon">üí¨</text>
                <text class="count">{{ company.reviewCount || 0 }}</text>
              </view>
              <text class="date">{{ formatTime(company.createTime) }}</text>
            </view>
            <text class="city-tag">{{ company.city }}</text>
          </view>
        </view>
      </template>
    </view>
    <view class="float-btns">
      <view class="float-btn glass-effect complaint-btn" @tap="goToComplaint">
        <text class="icon">üì¢</text>
      </view>
      <view class="float-btn glass-effect" @tap="goToAdd">
        <text class="icon">‚úèÔ∏è</text>
      </view>
    </view>
    <!-- ÂÖçË¥£Â£∞ÊòéÂºπÁ™ó -->
    <uni-popup ref="disclaimerPopup" type="center">
      <view class="disclaimer-popup">
        <view class="popup-header">
          <text class="title">ÂÖçË¥£Â£∞Êòé</text>
        </view>
        <view class="popup-content">
          <text class="text">1. Êú¨Âπ≥Âè∞‰ªÖÊèê‰æõ‰ø°ÊÅØÂàÜ‰∫´ÊúçÂä°ÔºåÊâÄÊúâÂÜÖÂÆπÂùáÊù•Ëá™Áî®Êà∑ÂèëÂ∏É„ÄÇ</text>
          <text class="text">2. Áî®Êà∑ÂèëÂ∏ÉÁöÑÂÜÖÂÆπ‰ªÖ‰ª£Ë°®‰∏™‰∫∫ËßÇÁÇπÔºåËØ∑ÁêÜÊÄßÂØπÂæÖ„ÄÇ</text>
          <text class="text">3. Â¶ÇÂèëÁé∞ËøùËßÑÂÜÖÂÆπÔºåËØ∑ÂèäÊó∂ËÅîÁ≥ªÊàë‰ª¨Â§ÑÁêÜ„ÄÇ</text>
          <text class="text">4. Âπ≥Âè∞‰øùÁïôÂØπËøùËßÑÂÜÖÂÆπÁöÑÂ§ÑÁêÜÊùÉÂà©„ÄÇ</text>
        </view>
        <view class="popup-footer">
          <button class="confirm-btn" @tap="closeDisclaimer">ÊàëÂ∑≤‰∫ÜËß£</button>
        </view>
      </view>
    </uni-popup>
    <view class="load-more" v-if="companies.length > 0">
      <view v-if="loading" class="loading">
        <view class="loading-icon"></view>
        <text class="loading-text">Âä†ËΩΩ‰∏≠...</text>
      </view>
      <text v-else-if="!hasMore" class="no-more">Ê≤°ÊúâÂ§ö‰∫Ü</text>
    </view>
    <!-- Ê∑ªÂä†ÊäïËØâÂºπÁ™óÁªÑ‰ª∂ -->
    <uni-popup ref="complaintPopup" type="center">
      <view class="complaint-popup glass-effect">
        <view class="popup-header">
          <text class="title">ÊäïËØâÂª∫ËÆÆ</text>
          <text class="close-icon" @tap="closeComplaintPopup">‚úï</text>
        </view>
        <view class="popup-content">
          <view class="content-item">
            <text class="icon">üìß</text>
            <view class="item-info">
              <text class="label">ÈÇÆÁÆ±ËÅîÁ≥ª</text>
              <text class="value">962723968@qq.com</text>
            </view>
            <button class="copy-btn" @tap="copyEmail">Â§çÂà∂</button>
          </view>
          <view class="content-item">
            <text class="icon">‚ö†Ô∏è</text>
            <view class="item-info">
              <text class="label">Ê∏©È¶®ÊèêÁ§∫</text>
              <text class="desc">Â¶ÇÂèëÁé∞‰ªª‰ΩïËøùËßÑÊàñ‰∏çÂΩìÂÜÖÂÆπÔºåËØ∑ÂèäÊó∂ËÅîÁ≥ªÊàë‰ª¨Â§ÑÁêÜÔºåÊàë‰ª¨‰ºöÂú®24Â∞èÊó∂ÂÜÖÁªô‰∫àÂõûÂ§ç„ÄÇ</text>
            </view>
          </view>
        </view>
        <view class="popup-footer">
          <button class="confirm-btn" @tap="closeComplaintPopup">ÊàëÁü•ÈÅì‰∫Ü</button>
        </view>
      </view>
    </uni-popup>
   
  </view>
</template>

<script setup lang="ts">
import { ref, onMounted } from 'vue'
import {
  onShow,
  onReachBottom,
  onPullDownRefresh,
  onPageScroll
} from '@dcloudio/uni-app';
import CompanySkeleton from '@/components/comment/CompanySkeleton.vue'
const companies = ref([])
const searchKeyword = ref('')
const disclaimerPopup = ref(null)
const pageSize = 10
const currentPage = ref(1)
const hasMore = ref(true)
const loading = ref(false)
const selectedCity = ref('')
const provinces = ref([])
const cities = ref([])
const selectedProvince = ref('')
const statusBarHeight = uni.getSystemInfoSync().statusBarHeight

// Ê†ºÂºèÂåñÊó∂Èó¥
const formatTime = (timestamp: number) => {
  const date = new Date(timestamp)
  return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`
}

// Ëé∑ÂèñÂÖ¨Âè∏ÂàóË°®
const getCompanyList = async (page = 1, isLoadMore = false) => {
  if (!isLoadMore) {
    loading.value = true
  }

  try {
    const { result } = await uniCloud.callFunction({
      name: 'getCompanyList',
      data: {
        page,
        pageSize,
        keyword: searchKeyword.value,
        city: selectedCity.value
      }
    })

    if (result.code === 0) {
      const processedData = result.data.map((item: any) => ({
        ...item,
        isExpanded: false
      }));
      
      if (isLoadMore) {
        companies.value = [...companies.value, ...processedData]
      } else {
        setTimeout(() => {
          companies.value = processedData
          loading.value = false
        }, 500)
      }
      hasMore.value = result.data.length === pageSize
      currentPage.value = page
    }
  } catch (error) {
    uni.showToast({
      title: 'Ëé∑ÂèñÊï∞ÊçÆÂ§±Ë¥•',
      icon: 'none'
    })
    loading.value = false
  }
}

// Âä†ËΩΩÊõ¥Â§ö
const loadMore = async () => {
  if (!hasMore.value || loading.value) return
  await getCompanyList(currentPage.value + 1, true)
}

// ÈáçÁΩÆÂàóË°®
const resetList = () => {
  companies.value = []
  currentPage.value = 1
  hasMore.value = true
  getCompanyList(1)
}

// ÊêúÁ¥¢Â§ÑÁêÜ
const handleSearch = () => {
  resetList()
}

// Ê∏ÖÈô§ÊêúÁ¥¢
const handleClear = () => {
  searchKeyword.value = ''
  resetList()
}

// Ë∑≥ËΩ¨Âà∞Êñ∞Â¢ûÈ°µÈù¢
const goToAdd = () => {
  uni.navigateTo({
    url: '/pages/comment/add/add-review'
  })
}

const goToDetail = (id: string) => {
  uni.navigateTo({
    url: `/pages/comment/detail/detail?id=${id}`
  })
}

// Ê£ÄÊü•ÊòØÂê¶È¶ñÊ¨°ËÆøÈóÆ
onMounted(() => {
  const hasShownDisclaimer = uni.getStorageSync('hasShownDisclaimer')
  if (!hasShownDisclaimer) {
    // È¶ñÊ¨°ËÆøÈóÆÔºåÊòæÁ§∫ÂÖçË¥£Â£∞Êòé
    disclaimerPopup.value.open()
  }
  getCompanyList()
})

// ÂÖ≥Èó≠ÂÖçË¥£Â£∞Êòé
const closeDisclaimer = () => {
  uni.setStorageSync('hasShownDisclaimer', true)
  disclaimerPopup.value.close()
}

const complaintPopup = ref(null)

const goToComplaint = () => {
  complaintPopup.value.open()
}

const closeComplaintPopup = () => {
  complaintPopup.value.close()
}

const copyEmail = () => {
  uni.setClipboardData({
    data: '962723968@qq.com',
    success: () => {
      uni.showToast({
        title: 'ÈÇÆÁÆ±Â∑≤Â§çÂà∂',
        icon: 'success'
      })
    }
  })
}

onReachBottom(() => {
  loadMore()
})

onMounted(() => {
  getCompanyList()
})

// Ëé∑ÂèñÁúÅ‰ªΩÂàóË°®
const getProvinces = async () => {
  try {
    const { result } = await uniCloud.callFunction({
      name: 'getCityList'
    })
    if (result.code === 0) {
      provinces.value = result.data
    }
  } catch (error) {
    uni.showToast({
      title: 'Ëé∑ÂèñÁúÅ‰ªΩÂ§±Ë¥•',
      icon: 'none'
    })
  }
}

// Ëé∑ÂèñÂüéÂ∏ÇÂàóË°®
const getCities = async (provinceCode: string) => {
  try {
    const { result } = await uniCloud.callFunction({
      name: 'getCityList',
      data: { province: provinceCode }
    })
    if (result.code === 0) {
      cities.value = result.data
    }
  } catch (error) {
    uni.showToast({
      title: 'Ëé∑ÂèñÂüéÂ∏ÇÂ§±Ë¥•',
      icon: 'none'
    })
  }
}

// ÊòæÁ§∫ÂüéÂ∏ÇÈÄâÊã©Âô®
const showCityPicker = () => {
  uni.showActionSheet({
    itemList: provinces.value.map(p => p.name),
    success: async (res) => {
      const province = provinces.value[res.tapIndex]
      selectedProvince.value = province.name
      await getCities(province.code)

      // ÈÄâÊã©ÂüéÂ∏Ç
      uni.showActionSheet({
        itemList: cities.value.map(c => c.name),
        success: (res) => {
          selectedCity.value = cities.value[res.tapIndex].name
          resetList()
        }
      })
    }
  })
}

onMounted(async () => {
  await getProvinces()
  getCompanyList()
})

// Âà§Êñ≠ÊñáÊú¨ÊòØÂê¶Ë∂ÖËøá5Ë°å
const isTextOverflow = (text: string) => {
  return text.length > 100
}

// ÂàáÊç¢Â±ïÂºÄ/Êî∂Ëµ∑Áä∂ÊÄÅ
const toggleExpand = (company: any) => {
  company.isExpanded = !company.isExpanded
}

// Ê∑ªÂä†ÁªüËÆ°Êï∞ÊçÆÁöÑÂìçÂ∫îÂºèÈáè
const totalReviews = ref(0)
const totalCompanies = ref(0)
const todayReviews = ref(0)

// ÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜ
const showTotalReviews = () => {
  uni.showToast({
    title: `ÊÄªËÆ° ${totalReviews.value} Êù°ÁÇπËØÑ`,
    icon: 'none'
  })
}

const showTotalCompanies = () => {
  uni.showToast({
    title: `Â∑≤Êî∂ÂΩï ${totalCompanies.value} ÂÆ∂‰ºÅ‰∏ö`,
    icon: 'none'
  })
}

const showTodayReviews = () => {
  uni.showToast({
    title: `‰ªäÊó•Êñ∞Â¢û ${todayReviews.value} Êù°ÁÇπËØÑ`,
    icon: 'none'
  })
}

// Ëé∑ÂèñÁªüËÆ°Êï∞ÊçÆ
const getStatistics = async () => {
  try {
    const { result } = await uniCloud.callFunction({
      name: 'getStatistics'
    })
    if (result.code === 0) {
      totalReviews.value = result.data.totalReviews
      totalCompanies.value = result.data.totalCompanies
      todayReviews.value = result.data.todayReviews
    }
  } catch (error) {
    uni.__f__('error','at pages/comment/index/index.vue:447','Ëé∑ÂèñÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error)
  }
}

// Âú® onMounted ‰∏≠Ë∞ÉÁî®
onMounted(async () => {
  await getStatistics()
  // ... ÂÖ∂‰ªñÂ∑≤ÊúâÁöÑ‰ª£Á†Å
})

// ‰∏ªÈ¢òÁõ∏ÂÖ≥
const currentTheme = ref('default')
const themes = [
  { name: 'default', label: 'ÈªòËÆ§Á¥´' },
  { name: 'blue', label: 'Ê∑±ÈÇÉËìù' },
  { name: 'green', label: 'Ëá™ÁÑ∂Áªø' },
  { name: 'red', label: 'ÁÉ≠ÊÉÖÁ∫¢' }
]

// ÂàáÊç¢‰∏ªÈ¢ò
const changeTheme = (theme: string) => {
  currentTheme.value = theme
  uni.setStorageSync('theme', theme)
}

// ÊòæÁ§∫‰∏ªÈ¢òÈÄâÊã©Âô®
const showThemePicker = () => {
  uni.showActionSheet({
    itemList: themes.map(t => t.label),
    success: (res) => {
      changeTheme(themes[res.tapIndex].name)
    }
  })
}

// ÂàùÂßãÂåñ‰∏ªÈ¢ò
onMounted(() => {
  const savedTheme = uni.getStorageSync('theme')
  if (savedTheme) {
    currentTheme.value = savedTheme
  }
})

</script>

