      }
    }
  }
  
  // ‰ºòÂåñÂÖ¨Âè∏ÂàóË°®
  .company-list {
    .company-card {
      margin-bottom: 24rpx;
      padding: 28rpx;
      border-radius: 20rpx;
      
      .card-header {
        margin-bottom: 20rpx;
