}

.header {
  position: sticky;
  top: 0;
  z-index: 100;
  padding: 20rpx 0;

  .search-box {
    border-radius: 16rpx;
    margin-bottom: 20rpx;
  }

  .location-picker {
    display: inline-flex;
    align-items: center;
    padding: 12rpx 24rpx;
    border-radius: 30rpx;

    .icon {
      margin-right: 8rpx;
      font-size: 28rpx;
    }

    .location-text {
      font-size: 28rpx;
      color: #333;
    }
  }
}

.company-card {
  border-radius: 20rpx;
  padding: 30rpx;
  margin-bottom: 30rpx;
  transition: all 0.3s ease;

  &-hover {
    transform: translateY(-2rpx);
    box-shadow: 0 12rpx 48rpx rgba(31, 38, 135, 0.15);
  }

  .card-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20rpx;

    .company-name {
      font-size: 32rpx;
      font-weight: 600;
      color: #1a1a1a;
      max-width: 70%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }

    .rating-wrapper {
      display: flex;
      align-items: center;
      flex-shrink: 0;

      .rating-text {
        margin-left: 12rpx;
        font-size: 28rpx;
        color: #FFB800;
        font-weight: 500;
      }
    }
  }

  .card-content {
    .cons-box {
      position: relative;
      background: rgba(255, 184, 0, 0.1);
      padding: 20rpx;
      border-radius: 12rpx;
      border-left: 4rpx solid #FFB800;

      .cons-text {
        font-size: 28rpx;
        color: #666;
        line-height: 1.6;
        
        &.truncated {
          display: -webkit-box;
          -webkit-line-clamp: 5;
          -webkit-box-orient: vertical;
          overflow: hidden;
        }
      }
      
      .expand-btn {
        display: inline-block;
        font-size: 26rpx;
        color: #3B7FFF;
        padding: 4rpx 20rpx;
        margin-top: 16rpx;
        background: rgba(59, 127, 255, 0.1);
        border-radius: 100rpx;
        transition: all 0.3s ease;
        
        &:active {
          transform: scale(0.95);
          background: rgba(59, 127, 255, 0.2);
        }
      }

      // Ê∑ªÂä†Ê∏êÂèòÈÅÆÁΩ©
      &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 60rpx;
        background: linear-gradient(to bottom, rgba(255, 184, 0, 0), rgba(255, 184, 0, 0.1));
        pointer-events: none; // Á°Æ‰øù‰∏çÂìçÁÇπÂáª‰∫ã‰ª∂
        opacity: 0;
        transition: opacity 0.3s;
      }

      // Âè™Âú®ÊñáÊú¨Ë¢´Êà™Êñ≠Êó∂ÊòæÁ§∫Ê∏êÂèòÈÅÆÁΩ©
      .cons-text.truncated + .expand-btn {
        & + .cons-box::after {
          opacity: 1;
        }
      }
    }
  }

  .card-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 20rpx;

    .stats {
      display: flex;
      align-items: center;
      gap: 20rpx;

      .stat-item {
        display: flex;
        align-items: center;

        .icon {
          margin-right: 6rpx;
          font-size: 24rpx;
        }

        .count {
          font-size: 24rpx;
          color: #666;
        }
      }

      .date {
        font-size: 24rpx;
        color: #999;
        margin-left: 20rpx;
      }
    }

    .city-tag {
      font-size: 24rpx;
      color: #666;
      background: rgba(0, 122, 255, 0.1);
      padding: 6rpx 16rpx;
      border-radius: 4rpx;
    }
  }
}

.float-btns {
  position: fixed;
  right: 40rpx;
  bottom: calc(40rpx + env(safe-area-inset-bottom));
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 100;

  .float-btn {
    width: auto; // Êîπ‰∏∫Ëá™ÈÄÇÂ∫îÂÆΩÂ∫¶
    min-width: 120rpx; // ËÆæÁΩÆÊúÄÂ∞èÂÆΩÂ∫¶
    height: 88rpx;
    margin-top: 20rpx;
    border-radius: 44rpx; // Êîπ‰∏∫ÂúÜËßíÁü©ÂΩ¢
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0 32rpx; // Ê∑ªÂä†Ê∞¥Âπ≥ÂÜÖËæπË∑ù
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
    
    .icon {
      font-size: 32rpx;
      margin-right: 8rpx; // ÂõæÊ†áÂíåÊñáÂ≠ó‰πãÈó¥ÁöÑÈó¥Ë∑ù
    }
    
    .text {
      font-size: 28rpx;
      color: #fff;
      white-space: nowrap; // Èò≤Ê≠¢ÊñáÂ≠óÊç¢Ë°å
    }

    &.complaint-btn {
      background: rgba(255, 59, 48, 0.1);
      
      .text {
        color: #FF3B30; // ÊäïËØâÊåâÈíÆÁöÑÊñáÂ≠óÈ¢úËâ≤
      }
    }

    &:active {
      transform: scale(0.95);
    }
  }
}

.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 120rpx 60rpx;

  .empty-image {
    width: 240rpx;
    height: 240rpx;
    margin-bottom: 40rpx;
  }

  .empty-text {
    font-size: 32rpx;
    color: #333;
    font-weight: 500;
    margin-bottom: 16rpx;
  }

  .empty-tips {
    font-size: 26rpx;
    color: #999;
    margin-bottom: 40rpx;
  }

  .empty-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 20rpx 40rpx;
    background: linear-gradient(135deg, #3B7FFF, #007AFF);
    border-radius: 40rpx;
    box-shadow: 0 8rpx 16rpx rgba(0, 122, 255, 0.1);
    font-size: 28rpx;
    color: #fff;
  }
}

.disclaimer-popup {
  background: #fff;
  width: 600rpx;
  border-radius: 20rpx;
  overflow: hidden;

  .popup-header {
    padding: 30rpx;
    text-align: center;
    border-bottom: 1rpx solid #eee;

    .title {
      font-size: 32rpx;
      font-weight: 500;
      color: #333;
    }
  }

  .popup-content {
    padding: 40rpx 30rpx;

    .text {
      font-size: 28rpx;
      color: #666;
      line-height: 1.8;
      display: block;
      margin-bottom: 20rpx;

      &:last-child {
        margin-bottom: 0;
      }
    }
  }

  .popup-footer {
    padding: 30rpx;
    border-top: 1rpx solid #eee;

    .confirm-btn {
      background: linear-gradient(135deg, #3B7FFF, #007AFF);
      color: #fff;
      border-radius: 100rpx;
      font-size: 28rpx;
      height: 80rpx;
      line-height: 80rpx;

      &:active {
        transform: scale(0.98);
      }
    }
  }
}

.load-more {
  padding: 30rpx;
  text-align: center;

  .loading {
    display: inline-flex;
    align-items: center;

    .loading-icon {
      width: 36rpx;
      height: 36rpx;
      margin-right: 12rpx;
      border: 3rpx solid #e5e5e5;
      border-top-color: #3B7FFF;
      border-radius: 50%;
      animation: spin 0.8s linear infinite;
    }

    .loading-text {
      font-size: 26rpx;
      color: #999;
    }
  }

  .no-more {
    font-size: 26rpx;
    color: #999;
  }
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

.company-list {
  opacity: 0;
  transform: translateY(20rpx);

  &.fade-in {
    animation: fadeInUp 0.6s ease forwards;
  }
}

.company-card {
  transform: translateY(20rpx);
  opacity: 0;
  animation: slideUp 0.5s ease forwards;
  animation-delay: calc(var(--index) * 0.1s);
}

.float-btns {
  .float-btn {
    animation: bounceIn 0.6s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(20rpx);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes slideUp {
  from {
    opacity: 0;
    transform: translateY(20rpx);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes bounceIn {
  from {
    transform: scale(0);
  }

  50% {
    transform: scale(1.1);
  }

  to {
    transform: scale(1);
  }
}

