}

.intro-section {
  padding: 0 30rpx;
  margin-bottom: 30rpx;

  .intro-cards {
    display: flex;
    gap: 20rpx;
    
    .intro-card {
      flex: 1;
      padding: 20rpx;
      border-radius: 16rpx;
      display: flex;
      align-items: center;
      justify-content: space-between;
      transition: all 0.3s ease;
      
      &:active {
        transform: scale(0.98);
      }
      
      .card-content {
        display: flex;
        flex-direction: column;
        
        .count {
          font-size: 32rpx;
          font-weight: 600;
          color: #333;
          margin-bottom: 4rpx;
          font-family: 'DIN';
        }
        
        .label {
          font-size: 24rpx;
          color: #666;
        }
      }
      
      .icon {
        font-size: 32rpx;
        opacity: 0.8;
      }
    }
  }
}

